import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, BehaviorSubject } from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class GetDataService {


  list:any
  topic:any
  private topics_source =new BehaviorSubject("")
  currentTopic=this.topics_source.asObservable() 


  constructor(private http:HttpClient) {  }

  getData(){
    return this.http.get("http://localhost:8081/getAll")}

  getTopic(topic){
    this.http.get("http://localhost:8081/"+topic).subscribe((data)=>{  
    this.topic=data})
  }

  changeTopic(topic:any){
    this.topics_source.next(topic)
  }

}
