import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FrontComponent } from './front/front.component';
import { LoginComponent } from './login/login.component';
import { LoginPageComponent } from './login-page/login-page.component';
import { RegisterPageComponent } from './register-page/register-page.component';
import { RegistrationComponent } from './registration/registration.component';
import { HomeComponent } from './home/home.component';
import { SidenavComponent } from './sidenav/sidenav.component';
import { ArrayComponent } from './array/array.component';
import { CollectionsComponent } from './collections/collections.component';
import { LinkedlistComponent } from './linkedlist/linkedlist.component';


const routes: Routes = [
  {path:'front',component:FrontComponent},
  {path:'register',component:RegistrationComponent},
  {path:'login',component:LoginComponent},
  {path:'registerPage',component:RegisterPageComponent},
  {path:'LoginPage',component:LoginPageComponent},
      {path:'',redirectTo: '/front',pathMatch:'full'},
      {path:"home",component:HomeComponent},
      {path:"sidenav",component:SidenavComponent},
      {path:"Basics of JAVA",component:ArrayComponent},
      {path:"Collections in Java",component:CollectionsComponent},
      {path:"LinkedList",component:LinkedlistComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
