import { Component, OnInit, Input } from '@angular/core';
import { GetDataService } from '../get-data.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-sidenav',
  templateUrl: './sidenav.component.html',
  styleUrls: ['./sidenav.component.css']
})
export class SidenavComponent implements OnInit {

  topics:any
  m_content:any

  constructor(public service : GetDataService,public router:Router) {
   }


  ngOnInit(): void {

    this.service.currentTopic.subscribe(data=>{
      this.topics=data
      this.m_content=this.topics.intro})
  }

  send_Content(content:any){
    this.m_content=content
  }

  onClick(){
    this.router.navigateByUrl("/"+this.topics.topic)
  }

}
